<template>
  <div class="match container" v-if="matchInfo">
    <div class="match--result">
        <div class="match--result-home">
            <img :src="matchInfo[0].teams.home.logo" alt="">
            <p>{{matchInfo[0].teams.home.name}}</p>
            <span>{{matchInfo[0].goals.home}}</span>
        </div>
        <div class="match--result-away">
            <span>{{matchInfo[0].goals.away}}</span>
            <img :src="matchInfo[0].teams.away.logo" alt="">
            {{matchInfo[0].teams.away.name}}
        </div>
    </div>
    <div class="match--events">
        <div class="match--event" v-for="(item, index) in matchInfo[0].events" :key="index">
            <div class="match--event-left" :style="item.team.id == matchInfo[0].teams.home.id ? 'opacity: 1' : 'opacity: 0'">
                <div class="match--event-card" v-if="item.type == 'Card'">
                    <div class="match--event-card-type" :class="item.detail == 'Yellow Card' ? 'match--event-card-type-yellow' : 'match--event-card-type-red'">
                    </div>
                    <div class="match--event-card-player">
                        {{item.player.name}}
                    </div>
                </div>
                <div class="match-event-goal" v-if="item.type == 'Goal'">
                    <img src="@/assets/img/goal.png" alt="">
                    {{item.player.name}}
                    <span v-if="item.detail == 'Penalty'">(P)</span>
                </div>
                <div class="match-event-subst" v-if="item.type == 'subst'">
                    <div class="in">
                        <img src="@/assets/img/subin.png" alt="">
                        {{item.player.name}}
                    </div>
                    <div class="out">
                        <img src="@/assets/img/subout.png" alt="">
                        {{item.assist.name}}
                    </div>
                </div>
            </div>
            <div class="match--event-center">{{item.time.elapsed}} '</div>
            <div class="match--event-right" :style="item.team.id == matchInfo[0].teams.away.id ? 'opacity: 1' : 'opacity: 0'">
                <div class="match--event-card" v-if="item.type == 'Card'">
                    <div class="match--event-card-type" :class="item.detail == 'Yellow Card' ? 'match--event-card-type-yellow' : 'match--event-card-type-red'">
                    </div>
                    <div class="match--event-card-player">
                        {{item.player.name}}
                    </div>
                </div>
                <div class="match-event-goal" v-if="item.type == 'Goal'">
                    <img src="@/assets/img/goal.png" alt="">
                    {{item.player.name}}
                    <span v-if="item.detail == 'Penalty'">(P)</span>
                </div>
                <div class="match-event-subst" v-if="item.type == 'subst'">
                    <div class="in">
                        <img src="@/assets/img/subin.png" alt="">
                        {{item.player.name}}
                    </div>
                    <div class="out">
                        <img src="@/assets/img/subout.png" alt="">
                        {{item.assist.name}}
                    </div>
                </div>
            </div>
            
        </div>
        <!-- <div class="match--events-right match--events-block">
            <div class="match--event" v-for="(item, index) in matchInfo[0].events" :key="index" v-show="item.team.id == matchInfo[0].teams.away.id">
                <div class="match--event-card" v-if="item.type == 'Card'">
                    <div class="match--event-card-type" :class="item.detail == 'Yellow Card' ? 'match--event-card-type-yellow' : 'match--event-card-type-red'">
                    </div>
                    <div class="match--event-card-player">
                        {{item.player.name}}
                    </div>
                </div>
                <div class="match-event-goals">

                </div>
            </div>
        </div> -->
    </div>
    <div class="match--statistics">
        <h2>Match statistics:</h2>
        <div class="match--statistics-block">
            <div class="match--info" v-for="(item, index) in matchInfo[0].statistics[0].statistics" :key="index">
                <span>{{item.type}}</span>
                <div class="match--info-block">
                    <div class="match--info-left">
                        <div  v-for="(info, index) in matchInfo[0].statistics[0].statistics" :key="index + '__1'" v-show="info.type == item.type">
                            <p>{{info.value !== null ? info.value : '-'}}</p>
                        </div>
                    </div>
                    <div style="margin-right: 8px; margin-left: 8px;">------------</div>
                    <div class="match--info-right">
                        <div v-for="(info, index) in matchInfo[0].statistics[1].statistics" :key="index + '__1'" v-show="info.type == item.type">
                            <p>{{info.value !== null ? info.value : '-'}}</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import {mapState} from 'vuex';

export default {
  name: 'MatchView',
  components: {
    
  },
  computed: {
    ...mapState(['matchInfo']),
  },
  mounted() {
    console.log(this.$route.params.id)
    this.$store.dispatch('getMatchInfo', this.$route.params.id)
    // this.$store.dispatch('getTeamInfo', obj)
    // this.$store.dispatch('getFixturesByTeamId', this.$route.params.id)
    // this.$store.dispatch('getPlayersInfo', this.$route.params.id)
  }
}
</script>

<style lang="scss">
