            margin-bottom: 10px;
            &-card {
                display: flex;
                align-items: center;
                &-type {
                    width: 16px;
                    height: 18px;
                    border-radius: 3px;
                    margin-right: 8px;
                    &-yellow {
                        background-color: yellow;
                    }
                    &-red {
                        background-color: red;
                    }
                }
            }
            &-left, &-right {
                float: left;
                width: 290px;
                padding: 5px;
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                white-space: nowrap;
            }
            &-left {
                text-align: right;
                margin-right: 5px;
                -webkit-box-pack: end;
                -ms-flex-pack: end;
                justify-content: flex-end;
            }
            &-right {
                text-align: left;
                -webkit-box-pack: start;
                -ms-flex-pack: start;
                justify-content: flex-start;
            }
            &-center {
                margin-right: 30px;
                margin-left: 30px;
                white-space: nowrap;
            }
        }
        &--events {
            max-width: 500px;
            margin: 0 auto;
            &-block {
                width: 50%;
                display: flex;
                font-size: 16pt;
                line-height: 48px;
            }
            // &-left {
            //     float: left;
            //     text-align: right;
            //     justify-content: flex-end;
            //     flex-direction: column;
            //     padding-right: 20px;
            // }
            // &-right {
            //     float: right;
            //     text-align: left;
            //     flex-direction: column;
            //     padding-left: 20px;
            // }
        }
        &--result {
            height: 100px;
            img {
                width: 30px;
                height: 30px;
                object-fit: contain;
                margin-right: 8px;
            }
            &-home {
                width: 50%;
                display: flex;
                font-size: 16pt;
                line-height: 48px;
                float: left;
                text-align: right;
                justify-content: flex-end;
                align-items: center;
                padding-right: 5px;
                span {
                    padding: 8px;
                    background-color: green;
                    height: 40px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-left: 8px;
                }
            }
