                line-height: 48px;
                float: right;
                text-align: left;
                align-items: center;
                padding-left: 5px;
                span {
                    margin-right: 8px;
                    padding: 8px;
                    background-color: green;
                    height: 40px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }
        }
        &--statistics {
            display: flex;
            align-items: center;
            flex-direction: column;
            margin-top: 30px;
            h2 {
                font-size: 40px;
                margin-bottom: 20px;
            }
            &-block {
                // display: flex;
                // align-items: center;
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                gap: 30px;
                .match--info {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    &-block {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        p {
                            color: #fff !important;
                        }
                    }
                    span {
                        font-weight: 700;
                        font-size: 24px;
                        color: #000;
                    }
                    p {
                        font-weight: 700;
                        font-size: 24px;
                        color: #000;
                    }
                }
                &-team {
                    color: rgb(238, 43, 76);
                    font-size: 24px;
                    font-weight: 700;
                }
                &-left {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    .match--info {
                        display: flex;
                        align-items: center;
                        span {
                            margin-right: 8px;
                            font-weight: 700;
                            font-size: 24px;
                            color: #000;
                        }
                        p {
                            margin-left: 8px;
                            font-weight: 700;
                            font-size: 24px;
                            color: #000;
                        }
                    }
                    margin-right: 50px;
                }
                &-right {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    .match--info {
                        display: flex;
                        align-items: center;
                        span {
                            margin-right: 8px;
                            font-weight: 700;
                            font-size: 24px;
                            color: #000;
                        }
                        p {
                            margin-left: 8px;
                            font-weight: 700;
                            font-size: 24px;
                            color: #000;
                        }
                    }
                }
            }
